import { configureStore, createSlice } from '@reduxjs/toolkit'

import user from './store/userSlice.js'

let counter = createSlice({
    name: 'counter',
    initialState: 0,
    reducers: {
        increment: (state) => state + 1,
        decrement: (state) => state - 1,
        reset: (state) => 0,
    }    
})




let stock = createSlice({
    name: 'stock',
    initialState: [10,11,12]
})

let cart = createSlice({
    name: 'cart',
    //initialState: [{id : 0, name : 'White and Black', count : 2},{id : 2, name : 'Grey Yordan', count : 1}] ,
    initialState: [] ,
    reducers: {
        //    //    count 
        addItem: (state, action) => {
            // let id = action.payload.id;
            let idx = state.findIndex(item => item.id === action.payload.id)
            console.log('@@ addItem idx=',idx);console.log('@@ addItem state=',state);
            console.log('@@ addItem action=',action);console.log('@@ addItem action.payload=',action.payload);
            console.log('@@ addItem action.payload.id=',action.payload.id);
            if(idx === -1) { //   
                state.push(action.payload) // count: 1  
            } else { //     
                state[idx].count += 1
            }
        },
        //   
        removeItem: (state, action) => {            
            let idx = state.findIndex(item => item.id === action.payload.id)
            console.log('@@ addItem idx=',idx);console.log('@@ addItem state=',state);
            console.log('@@ addItem action=',action);console.log('@@ addItem action.payload=',action.payload);
            console.log('@@ addItem action.payload.id=',action.payload.id);
            if(idx === -1) { //  
                state.splice(action.payload)
            } else { //   count   
                state[idx].count -= 1 
                if(state[idx].count === 0) {
                    state.splice(idx,1) //  0 
                }
            }
        },
        increment: (state, action) => {
            let idx = state.findIndex(item => item.id === action.payload)
            if(idx !== -1) { // 
                state[idx].count++
            } //return {...state,count:state[action.payload].count++}
        },
        //     //  0 
        decrement: (state, action) => {
            let idx = state.findIndex(item => item.id === action.payload)
            if(idx !== -1) { // 
                (state[idx].count > 0) && state[idx].count-- //    true  true ~
                if(state[idx].count === 0) { //  0 
                    state.splice(idx,1) //  0  id      
                } 
            }
        },
        //    
        delItem: (state, action) => {
            let idx = state.findIndex(item => item.id === action.payload)
            if(idx !== -1) { // 
                    state.splice(idx,1)  //  0  id  
                } 
        },
        changeCount: (state, action) => {
            state[action.payload.index].count = action.payload.count
        },
        reset: (state, action) => {
            state.splice(0,state.length)
        }
    }
})
export let {increment, decrement, addItem, removeItem, delItem, changeCount} = cart.actions

export default configureStore({
  reducer: { 
    counter: counter.reducer,
    user: user.reducer,
    stock: stock.reducer,
    cart: cart.reducer,

  }
}) 